.join__image {
  object-fit: contain;
  position: absloute;
  width: 100%;
  height: auto;
}

/* ----------
STATIC PAGE VIEW 
- /about
- /howto
- /charter
---------- */
.static-page__wrapper {
  background: $primary-theme-color;
  padding: 2em 1em;
  max-width: 840px;
  margin: 0 auto;
}

.static-page__section {
  margin-bottom: 5em;
}

.static-page__main-header {
  color: $primary-highlight-color;
  font-family: $display-font;
  font-size: 36px;
  font-weight: 200;
  line-height: 42px;
}

@media (min-width: 600px) {
  .static-page__wrapper {
    padding: 2em 4em;
  }

  .static-page__main-header {
    font-size: 76px;
    line-height: 89px;
  }
}

.static-page__main-header--small {
  color: $primary-highlight-color;
  font-size: 24px;
  line-height: 30px;
  margin-top: 0.5em;
}

@media (min-width: 600px) {
  .static-page__main-header--small {
    font-size: 36px;
    line-height: 45px;
  }
}

.static-page__sub-header {
  @include section-title;
  border-bottom: 1px solid $primary-text-color;
  margin: 3em 0em 0.5em 0em;
  padding-bottom: 0.25em;
  text-transform: uppercase;
  width: 100%;
}

.static-page__sub-header--no-underline {
  border-bottom: 0;
  padding-bottom: 0;
}

.static-page__copy {
  font-size: 18px;
  font-weight: 300;
  line-height: 22px;
  margin-bottom: 1em;
}

@media (min-width: 600px) {
  .static-page__copy {
    font-size: 20px;
    line-height: 25px;
  }
}

.static-page__copy--bold {
  font-weight: bold;
}

.static-page__link {
  color: $primary-text-color;
  text-decoration: underline;
}

.static-page__link--highlight {
  color: $primary-highlight-color;
  text-decoration: underline;
}

.static-page__link--highlight:hover {
  color: $primary-text-color;
}

.static-page__sub-header--margin_fix {
  margin-bottom: 2em;
}

.static-page__partner-link {
  color: $primary-text-color;
}

.static-page__partner-link:hover {
  color: $primary-highlight-color;
}

/* ---------- 
ABOUT VIEW  
---------- */
.about__about-trusat-section {
  font-family: $display-font;
  font-size: 20px;
  font-weight: $light-weight;
  line-height: 25px;
}

.about__deck {
  margin-top: 1em;
}

.about__block-wrapper {
  margin-top: 4em;
}

.about__block-pair {
  display: block;
  margin-bottom: 2em;
}

@media (min-width: 600px) {
  .about__block-pair {
    display: flex;
  }
}

@media (min-width: 600px) {
  .about__block--left {
    width: 200px;
    flex: 0 0 250px;
  }
}

@media (min-width: 600px) {
  .about__block--right {
    padding-left: 1em;
    padding-top: 0.15em; //Fix for mystery misalignment
    flex: 1 1 230px;
    width: 200px;
  }
}

.about__illustration {
  height: auto;
  width: 100%;
}

.about__sub-header--top {
  margin-top: 1em;
}

/* ----------
HOW TO VIEW 
---------- */
.how-to__wrapper {
  font-family: $display-font;
  font-size: 20px;
  font-weight: $light-weight;
  line-height: 25px;
}

.how-to__sub-header--top {
  margin-top: 0.5em;
}

.how-to__step-wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 1em;
}

@media (min-width: 600px) {
  .how-to__step-wrapper {
    flex-direction: row;
  }
}

@media (min-width: 600px) {
  .how-to__step-wrapper {
    border-top: 1px solid $tertiary-color;
    padding-top: 1em;
    margin-bottom: 2em;
  }
}

.how-to__step-number {
  flex-basis: 50px;
  min-width: 50px;
  font-size: $extra-large-font-size;
}

@media (max-width: 600px) {
  .how-to__step-icon-wrapper {
    max-width: 100px;
  }
}

@media (min-width: 600px) {
  .how-to__step-icon-wrapper {
    min-width: 100px;
  }
}

@media (min-width: 600px) {
  .how-to__step-image-wrapper {
    min-width: 200px;
    width: 200px;
  }
}

.how-to__step-description {
  padding-left: 0;
}

@media (min-width: 600px) {
  .how-to__step-description {
    padding-left: 1em;
  }
}

.how-to__screenshot {
  width: 100%;
}

@media (min-width: 600px) {
  .how-to__sub-header--top {
    margin-top: 1em;
  }
}

// stops sign up button wrapping over copy below
.how-to__copy--with-margin {
  margin-top: 4em;
}

@media (min-width: 400px) {
  .how-to__copy--with-margin {
    margin-top: 1em;
  }
}

.how-to__ul {
  display: flex;
  flex-direction: column;
  list-style: disc outside;
  margin-left: 1.1em;
  font-size: 20px;
  line-height: 25px;
}

.how-to__ul > li {
  margin-bottom: 0.5em;
}

/* ----------
CHARTER VIEW
---------- */
.charter__sub-header {
  margin-top: 1em;
}

/* ----------
FAQ view
---------- */
.faq__wrapper {
  font-family: $display-font;
  font-size: 20px;
  font-weight: $light-weight;
  line-height: 25px;
}

.faq__sub-header {
  margin-top: 1em;
}

.faq__question {
  font-family: $main-font;
  font-size: 20px;
  font-weight: bold;
  line-height: 25px;
  letter-spacing: 0.05em;
  margin-bottom: 0.5em;
  margin: 1em 0em 0.5em 0em;
}

.faq__answer {
  font-family: $main-font;
  line-height: 27px;
  margin-bottom: 2em;
}

.welcome__border-left {
  border-left: 1px solid $tertiary-color;
}
