/* ----------
ACCOUNT SETTINGS
---------- */
.account-settings__wrapper {
  margin: 2em 1em;
}

.account-settings__header {
  @include view-title;
}

@media (min-width: 600px) {
  .account-settings__wrapper {
    margin: 3em 4em;
    max-width: 1200px;
  }
  .account-settings__header {
    @include view-title--desktop;
  }
}

.account-settings__button-wrapper {
  margin-top: 2em;
  display: flex;
}

.account-settings__cancel-button {
  margin-right: 1em;
}

.account-settings__log-out-button {
  margin-top: 2em;
}

/* ----------
PROFILE SETTINGS
---------- */
.profile-settings__wrapper {
  display: flex;
  justify-content: space-between;
  flex-wrap: wrap;
}

.profile-settings__heading {
  @include section-title;
  align-items: center;
  border: 1px solid $secondary-theme-color;
  border-left: none;
  border-right: none;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1em;
  padding: 0.5em 0;
}

.profile-settings__edit-button-text {
  color: $secondary-theme-color;
  font-style: normal;
  font-weight: normal;
  font-size: $small-font-size;
  line-height: $medium-line-height;
  text-decoration: underline;
}

.profile-settings__edit-button-text:hover {
  cursor: pointer;
}

.profile-settings__profile-wrapper {
  margin-top: 3em;
  width: 100%;
}

@media (min-width: 600px) {
  .profile-settings__profile-wrapper {
    margin-top: 1em;
    max-width: 500px;
  }
}

.profile-settings__setting-wrapper {
  display: flex;
  margin: 0.5em 0;
}

.profile-settings__setting-label {
  color: $secondary-theme-color;
  font-size: $extra-small-font-size;
  line-height: $medium-line-height;
  margin-right: 1em;
}

.profile-settings__setting-value {
  line-height: $medium-line-height;
}

.edit-profile-settings-input {
  background: black;
  border-radius: 4px;
  border: 1px solid #cccccc;
  color: $primary-text-color;
  padding: 1em;
  width: 100%;
}

.profile-settings__text-area {
  background: black;
  border-radius: 4px;
  border: 1px solid #cccccc;
  color: $primary-text-color;
  height: 80px;
  padding: 1em;
  resize: none;
  width: 100%;
}

@media (min-width: 600px) {
  .profile-settings__text-area {
    height: auto;
    resize: none;
    width: 100%;
  }
}

.profile-settings__station-text-wrapper {
  display: flex;
}

.profile-settings__station-text {
  margin-right: 0.5em;
}

/* ----------
SAVED LOCATIONS
---------- */
.saved-locations__wrapper {
  color: #a5a5a5;
  display: flex;
  flex-direction: column;
  margin-top: 3em;
  text-align: left;
  width: 100%;
}

.saved-locations__heading {
  @include section-title;
  align-items: center;
  border: 1px solid $secondary-theme-color;
  border-left: none;
  border-right: none;
  display: flex;
  justify-content: space-between;
  margin-bottom: 0.5em;
  padding: 0.5em 0;
}

@media (min-width: 600px) {
  .saved-locations__wrapper {
    margin-top: 1em;
    max-width: 600px;
  }
}

.locations-table__header-row {
  border-bottom: 1px solid #4f4f4f;
}

.locations-table__table-data {
  padding: 0.5em 1em 0.5em 0em;
}

.locations-table__name-data {
  color: $primary-text-color;
  font-size: $small-font-size;
  padding: 0.5em 1em 0.5em 0em;
}

.locations-table__delete-symbol:hover {
  cursor: pointer;
}

.saved-locations__button-wrapper {
  align-self: flex-end;
  margin-top: 1em;
}

.saved-locations__cancel-button {
  margin-right: 1em;
}

/* ----------
DATA SETTINGS
---------- */
.data-settings__wrapper {
  display: flex;
  flex-direction: column;
  margin-top: 3em;
}

.data-settings__heading,
.security-settings__heading {
  @include section-title;
  border: 1px solid $secondary-theme-color;
  border-left: none;
  border-right: none;
  display: flex;
  justify-content: space-between;
  margin-bottom: 1em;
  padding: 0.5em 0;
}

.data-settings__button-wrapper {
  align-self: flex-start;
  display: flex;
  flex-direction: column;
}

.data-settings__remove-text {
  border: 1px solid $primary-text-color;
  border-radius: 4px;
  color: $primary-text-color;
  padding: 0.75em;
  text-align: center;
}

/* ----------
DownloadObservations
---------- */
.download-observations {
  border: 1px solid $primary-text-color;
  border-radius: 4px;
  color: $primary-text-color;
  display: inline-block;
  margin: 0em 1em 1em 0em;
  padding: 0.75em;
  text-align: center;
}

.download-observations:hover {
  cursor: pointer;
}

/* ----------
SECURITY SETTINGS
---------- */
.security-settings__wrapper {
  margin-top: 3em;
}

.security-settings__info-text {
  @include body-text--medium;
  margin-bottom: 1em;
  max-width: 1000px;
}

/* ----------
METAMASK IMPORT
---------- */
.metamask-import__wrapper {
  margin: 3em 4em;
}

.metamask-import__link-to-account-text {
  color: #a5a5a5;
  font-size: $extra-small-font-size;
  padding: 0.5em;
  text-decoration: underline;
}

.metamask-import__link-to-account-text:hover {
  cursor: pointer;
}

.metamask-import__main-header {
  @include view-title--desktop;
  margin-bottom: 0.25em;
}

.metamask-import__step-header-wrapper {
  display: flex;
}

.metamask-import__step-header--todo {
  font-weight: bold;
  font-size: $extra-large-font-size;
}

.metamask-import__step-header--done {
  color: $secondary-theme-color;
  font-weight: bold;
  font-size: $extra-large-font-size;
}

.metamask-import__circle-check {
  margin-left: -1.3em;
}

.metamask-import__copy-wrapper {
  margin-top: 1em;
  max-width: 1000px;
}

.metamask-import__copy {
  font-size: $large-font-size;
  margin: 0.5em 0;
}

.metamask-import__link-text--white {
  color: $primary-text-color;
  text-decoration: none;
}

.metamask-import__button-wrapper {
  margin: 2em 0;
}

.metamask-import__link-text--black {
  color: $primary-theme-color;
  text-decoration: none;
}

.metamask-import__back-button {
  margin-right: 1em;
}

.metamask-import__private-key-wrapper {
  border: 1px solid $primary-text-color;
  border-radius: 4px;
  display: inline-flex;
  flex-wrap: wrap;
  padding: 1em;
}

.metamas-import__private-key-text {
  font-weight: 500;
  font-size: $medium-font-size;
}

/* ----------
SECRET FORM - in MetamaskImport component
---------- */
.secret-form__label {
  color: $secondary-theme-color;
}
